<template>
  <div class="card">
    <div class="card-content">
      <div class="media">
        <div class="media-left">
          <figure class="image is-48x48">
            <img src="https://bulma.io/images/placeholders/96x96.png" alt="Player's picture">
          </figure>
        </div>
        <div class="media-content">
          <p class="title is-4" v-text="player.name"></p>
          <p v-if="player.jerseyNumber" class="subtitle is-6">Nº: <span v-text="player.jerseyNumber"></span></p>
          <p v-if="player.nationality" class="title is-5" v-text="player.nationality"></p>
        </div>
      </div>

      <div class="content">
        <p v-if="player.position">Position: <span v-text="player.position"></span></p>
        <p v-if="player.marketValue">Market Value: <span v-text="player.marketValue"></span></p>
        Birthday: <time :datetime="player.dateOfBirth" v-text="player.dateOfBirth"></time> <small>( {{ player.dateOfBirth | age }} years ) </small>
      </div>
    </div>
  </div>
</template>
<script>
import DateParsers from '@/mixins/DateParsers';

export default {
  name: 'Player',
  mixins: [DateParsers],
  props: {
    player: {
      type: Object,
      required: true,
    },
  },
};
</script>




