<template>
  <div>
    <div class="hero is-primary">
      <div class="hero-body columns level">
          <div class="column is-2 level-item">
          <figure class="is-pulled-right image title is-64x64">
            <img :alt="team.name" :src="team.crestUrl">
          </figure>
        </div>
        <span class="column is-9 level-item">
          <span class="title" v-text="team.name"></span>
          <span class="subtitle"> ( <span v-text="playersLength"></span> players )</span>
        </span>
      </div>
    </div>
    <div class="section container">
      <div class="field is-grouped is-grouped-right" @click.prevent="sorted">
        <p class="control">
          <a class="button is-light" data-value="age">Age</a>
        </p>
        <p class="control">
          <a class="button is-light" data-value="squadMarketValue">Market Value</a>
        </p>
        <p class="control">
          <a class="button is-light is-active is-inverted" data-value="jerseyNumber">Jersey Number</a>
        </p>
      </div>
      <div v-if="loading">Loading...</div>
      <div class="columns is-multiline" v-else>
        <div class="column is-3" v-for="player in sortedPlayers" :key="player.id">
          <player :player="player"></player>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import Player from "./Player";
import { TEAM_PLAYERS } from '@/store/modules/competitions/loading-types';

export default {
  name: "Team",
  components: {
    Player,
  },
  props: {
    id: {
      required: true
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => vm.updateTeam());
  },
  beforeRouteUpdate(to, from, next) {
    this.updateTeam();
    next();
  },
  watch: {
    $route: 'updateTeam',
  },
  data() {
    return {
      orderBy: null,
      orderDirection: 1,
    };
  },
  computed: {
    loading() {
      return this.$store.getters.isLoading(TEAM_PLAYERS);
    },
    playersLength() {
      return this.players ? this.players.length : 0;
    },
    sortedPlayers() {
      const players = this.players.slice();
      return players.slice().sort((p1, p2) => {
        switch(this.orderBy) {
          case 'age':
              return new Date(p1.dateOfBirth) - new Date(p2.dateOfBirth);
          case 'squadMarketValue':
              return p1.squadMarketValue - p2.squadMarketValue;
          default:
              return p1.jerseyNumber - p2.jerseyNumber;
        }
      });
    },
    ...mapGetters({
      team: 'getTeam',
      players: "getTeamPlayers",
    })
  },
  methods: {
    updateTeam() {
      this.$store.dispatch("fetchTeam", { id: this.id });
      this.$store.dispatch("fetchTeamPlayers", { id: this.id });
    },
    sorted(event) {
      this.orderBy = event.target.getAttribute('data-value');
      const buttons = event.currentTarget.getElementsByClassName('button');
      for (const button of buttons) {
        button.classList.remove('is-active');
        button.classList.remove('is-inverted');
      }
      event.target.classList.toggle('is-active')
      event.target.classList.toggle('is-inverted')
    },
  }
};
</script>



